@import "./src/assets/sass/variables";

.portfolio {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
  overflow-y: auto;
  left: 0 !important;
  padding: 0;
  margin: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: $bgSecondary;

  .portfolioInner {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;

    .example {
      min-height: 500px;
      background-color: $bgSecondary;
    }

    .example2 {
      height: 800px;
      background-color: $bgSecondary;
    }
  }

}

.animation {
  animation: animation 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@keyframes animation {
  0% {
    transform: rotateY(-20deg) rotateX(-35deg) translate(-300px, 300px) skew(-35deg, 10deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(0) rotateX(0deg) translate(0, 0) skew(0deg, 0deg);
    opacity: 1;
  }
}



